
.title{
    background-color: brown;
}
*{
    margin: 0;
    padding: 0;
}
.navbar{
    display: flex;
    align-items: center;
    justify-content: center;
    position: sticky;
    top: 0;
    cursor: pointer;
}

.nav-list{
    width: 50%;
    display: flex;
    align-items: center;

}

.nav-list li{
    list-style: none;
    padding: 26px 30px;

}
.nav-list li a,a{
    text-decoration: none;
    color: white;
    

}
.nav-list li a:hover{
    
    color: gray;

}
.rightNav{
    
    text-align: right;
    width: 50%;
}

#search{
    padding-left: 0px;
    margin: 0px;
    font-size: 18px;
    border: 2px solid gray;
    border-radius: 2px;
    background: burlywood;
}
.background{
    background-color: blueviolet;
    /* background: rgba(0, 0, 0, 0.7) url('../res/bg2.jpg'); */
    background-size: cover;
    background-blend-mode: darken;
    
}
.btn{
    color:aliceblue;
    font-weight: bold;
    margin: 7px;
    padding: 8px 20px;
    border: 2px solid white;
    border-radius: 8px;
    background-color: indianred ;
    opacity: 80%;
}
#date,#title,#amount{
    background-color: aliceblue;
    width: 20%;
    text-align: center;
    align-items:flex-end;
    margin-left: 400px;
    margin-top: 10px;
    margin-bottom: 0px;
    border: 2px solid black;
    border-radius: 2px;

}